"use client"

import { useState } from "react"
import CreateLiveModal from "./CreateLiveModal"
import { createLiveClass } from "@/services/liveClassService"

export default function CreateLiveButton() {
  const [showModal, setShowModal] = useState(false)

  const handleCreateLive = async (liveData) => {
    try {
      await createLiveClass(liveData)
      window.location.reload()
    } catch (error) {
      console.error(error)
    }
  }

  return (
    <>
      <div
        onClick={() => setShowModal(true)}
        className="bg-white rounded-2xl overflow-hidden shadow-sm cursor-pointer hover:shadow-md transition-shadow border-2 border-dashed border-gray-200 hover:border-blue-300"
      >
        <div className="h-48 bg-gradient-to-br from-blue-50 to-blue-100 relative flex items-center justify-center">
          <div className="text-center">
            <div className="w-16 h-16 bg-blue-500 rounded-full flex items-center justify-center mb-4 mx-auto">
              <svg className="w-8 h-8 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
              </svg>
            </div>
            <p className="text-blue-500 font-semibold text-lg">Create New Live</p>
          </div>
        </div>
        <div className="p-4">
          <div className="text-center">
            <p className="text-blue-500 font-medium">Create New Live +</p>
            <p className="text-sm text-gray-500 mt-1">Click to add a new live session</p>
          </div>
        </div>
      </div>

      {showModal && <CreateLiveModal onClose={() => setShowModal(false)} onSubmit={handleCreateLive} />}
    </>
  )
}
